@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-bg: #1C0B19
}
body {
  background-color: #1C0B19;
  background-color: var(--color-bg);
  font-family: 'Raleway', Arial, Helvetica, sans-serif;
}

.title {
  text-transform: uppercase;
  text-align: center;
  text-shadow: 3px 4px 5px black;
  color: white;
}

.title i {
  color: white;
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
@media (max-width: 768px) {
  /* For mobile phones: */
  .container {
    margin: 5%;
  }
  .footer {
    margin: 10%;
  }
}
.footer {
  color: white;
}
.footer a {
  text-decoration: none;
  background-color: white;
  border-radius: 1em;
  color: #1C0B19;
  color: var(--color-bg);
  padding: 0.5em;
  font-weight: bold;
}
.footer i{
  padding: 0.3em;
  background-color: #1C0B19;
  background-color: var(--color-bg);
  color: white;
  border-radius: 100%;
  font-weight: normal!important;
}
:root {
  --color-tweet: #4ea699;
  --color-text: #140d4f;
}
#quote-box {
  background-color: white;
  padding: 1.5em;
  border-radius: 1.5em;
  color: black;
  font-family: "Raleway";
  max-width: 30em;
  overflow: hidden;
  height: auto;
  box-shadow: 0px 10px 33px -5px rgba(0, 0, 0, 0.75);
}

#text {
  text-align: justify;
  font-size: 1.4em;
  word-wrap: break-word;
}

#text i {
  color: #140d4f;
  color: var(--color-text);
  margin: 0;
  padding: 0;
}
#author {
  margin-top: 0.5em;
  margin-left: 1em;
  font-size: 1.2em;
  font-weight: bold;
  color: #140d4f;
  color: var(--color-text);
}

#new-quote button {
  outline: none;
  border: 0;
  padding: 0.5em 1em 0.5em 1em;
  border-radius: 0.2em;
  font-weight: bold;
  font-family: "Raleway";
  float: left;
  cursor: pointer;
  font-size: inherit;
  background-color: #140d4f;
  background-color: var(--color-text);
  color: white;
  transition: 0.3s;
}

#new-quote button:hover {
  background-color: white;
  color: #140d4f;
  color: var(--color-text);
  box-shadow: inset 0px 0px 0px 2px #140d4f;
  box-shadow: inset 0px 0px 0px 2px var(--color-text);
}

#tweet-quote {
  text-decoration: none;
  color: white;
  padding: 0.5em 1em 0.5em 1em;
  background-color: #4ea699;
  background-color: var(--color-tweet);
  border-radius: 0.2em;
  float: right;
  font-weight: bold;
  max-width: 1em;
  overflow: hidden;
  transition: max-width 1s linear;
  -webkit-transition: max-width 1s linear;
  -moz-transition: max-width 1s linear;
  white-space: nowrap;
}

#tweet-quote:hover {
  background-color: white;
  color: #4ea699;
  color: var(--color-tweet);
  box-shadow: inset 0px 0px 0px 2px #4ea699;
  box-shadow: inset 0px 0px 0px 2px var(--color-tweet);
  max-width: 100%;
}
#tweet-quote:hover::after {
  content: "Twittear";
  margin-left: 0.5em;
}

@media (max-width: 768px) {
  /* For mobile phones: */
  #quote-box {
    min-width: 90%;
  }
}

